
:root {
    --font-color: #333333;
    --card-color: #efeff2;
    --btn-color: #c0bac7;
}

ol, ul {
    list-style: none;
  }
  
  table {
    border-collapse: collapse;
    border-spacing: 0;
  }
  
  caption, th, td {
    text-align: left;
    font-weight: normal;
    vertical-align: middle;
  }
  
  q, blockquote {
    quotes: none;
  }
  q:before, q:after, blockquote:before, blockquote:after {
    content: "";
    content: none;
  }
  
  a img {
    border: none;
  }
  
  article, aside, details, figcaption, figure, footer, header, hgroup, main, menu, nav, section, summary {
    display: block;
  }
  

  
  img {
    max-width: 100%;
  }
  
  .cf:before, .cf:after {
    content: " ";
    display: table;
  }
  
  .cf:after {
    clear: both;
  }
  
  .cf {
    *zoom: 1;
  }
  
  .wrap {
    width: 75%;
    max-width: 960px;
    margin: 0 auto;
    padding: 5% 0;
    margin-bottom: 5em;
    padding-top: 100px;
  }
  
  .checkout-title {
    font-weight: bold;
    text-align: center;
    font-size: 2em;
    padding: 1em 0;
    border-bottom: 1px solid #dadada;
    letter-spacing: 3px;
    text-transform: uppercase;
  }
  .checkout-title span {
    font-family: "Droid Serif", serif;
    font-weight: normal;
    font-style: italic;
    text-transform: lowercase;
    color: #777;
  }
  
  .heading {
    padding: 1em 0;
    border-bottom: 1px solid #D0D0D0;
  }
  .heading h1 {
    font-size: 2em;
    float: left;
  }
  .heading a.continue:link {
    text-decoration: none;
    color: #ffffff;
    padding: 0.8rem;
    font-size: 14px;
    text-transform: uppercase;
    font-weight: 400;
    float: right;
    text-align: right;
    transition: all 0.25s linear;
    background: var(--btn-color);
    letter-spacing: 0.3em;
    cursor: pointer;

  }

  .heading a {
    color: #ffffff;
  }
  
  .tableHead {
    display: table;
    width: 100%;
    font-size: .75em;
  }
  .tableHead li {
    display: table-cell;
    padding: 1em 0;
    text-align: center;
  }
  .tableHead li.prodHeader {
    text-align: left;
  }
  
  .cart {
    padding: 1em 0;
  }
  .cart .items {
    display: block;
    width: 100%;
    padding: 1.5em;
    border-bottom: 1px solid #fafafa;
  }
  .cart .items.even {
    background: #fafafa;
  }
  .cart .items .infoWrap {
    display: table;
    width: 100%;
  }
  .cart .items .cartSection {
    display: table-cell;
    vertical-align: middle;
  }
  .cart .items .cartSection .itemNumber {
    font-size: .75em;
    color: #777;
    margin-bottom: .5em;
  }
  .cart .items .cartSection h3 {
    font-size: 1em;
   
    font-weight: bold;
    text-transform: uppercase;
    letter-spacing: .025em;
  }
  .cart .items .cartSection p {
    display: inline-block;
    font-size: .85em;
    color: #777777;
  
  }
  .cart .items .cartSection p .quantity {
    font-weight: bold;
    color: #333;
  }
  .cart .items .cartSection p.stockStatus {
    color: #82CA9C;
    font-weight: bold;
    padding: .5em 0 0 1em;
    text-transform: uppercase;
  }
  .cart .items .cartSection p.stockStatus.out {
    color: #F69679;
  }
  .cart .items .cartSection .itemImg {
    width: 4em;
    float: left;
  }
  .cart .items .cartSection.qtyWrap, .cart .items .cartSection.prodTotal {
    text-align: center;
  }
  .cart .items .cartSection.qtyWrap p, .cart .items .cartSection.prodTotal p {
    font-weight: bold;
    font-size: 1.25em;
  }
  .cart .items .cartSection input.qty {
    width: 2em;
    text-align: center;
    font-size: 1em;
    padding: .25em;
    margin: 1em .5em 0 0;
  }
  .cart .items .cartSection .itemImg {
    width: 8em;
    display: inline;
    padding-right: 1em;
  }
  
  .special {
    display: block;
  
  }
  .special .specialContent {
    padding: 1em 1em 0;
    display: block;
    margin-top: .5em;
    border-top: 1px solid #dadada;
  }
  .special .specialContent:before {
    content: "\21b3";
    font-size: 1.5em;
    margin-right: 1em;
    color: #6f6f6f;
  }
  
  .remove {
    text-decoration: none;
    color: var(--btn-color);
    font-weight: bold;
    background: white;
    border: none;
    padding: .5em;
    font-size: .75em;
    display: inline-block;
    line-height: .85;
  }
  .remove:hover {
    color: #f30;
  }
  
  .promoCode {
    border: 2px solid #efefef;
    float: left;
    width: 35%;
    padding: 2%;
  }
  .promoCode label {
    display: block;
    width: 100%;
    font-style: italic;
    font-size: 1.15em;
    margin-bottom: .5em;
    letter-spacing: -.025em;
  }
  .promoCode input {
    width: 85%;
    font-size: 1em;
    padding: .5em;
    float: left;
    border: 1px solid #dadada;
  }
  .promoCode input:active, .promoCode input:focus {
    outline: 0;
  }
  .promoCode a.btn {
    float: left;
    width: 15%;
    padding: .75em 0;
    border-radius: 0 1em 1em 0;
    text-align: center;
    border: 1px solid #82ca9c;
  }
  .promoCode a.btn:hover {
    border: 1px solid #f69679;
    background: #f69679;
  }
  
  .btn:link, .btn:visited {
    text-decoration: none;
    font-family: "Montserrat", sans-serif;
    letter-spacing: -.015em;
    font-size: 1em;
    padding: 1em 3em;
    color: #fff;
    background: #82ca9c;
    font-weight: bold;
    border-radius: 50px;
    float: right;
    text-align: right;
    -webkit-transition: all 0.25s linear;
    -moz-transition: all 0.25s linear;
    -ms-transition: all 0.25s linear;
    -o-transition: all 0.25s linear;
    transition: all 0.25s linear;
  }
  .checkout-btn:after {
    content: "\276f";
    padding: .5em;
    position: relative;
    right: 0;
    -webkit-transition: all 0.15s linear;
    -moz-transition: all 0.15s linear;
    -ms-transition: all 0.15s linear;
    -o-transition: all 0.15s linear;
    transition: all 0.15s linear;
  }
  .checkou-btn:hover, .checkout-btn:focus, .checkout-btn:active {
    background: #f69679;
  }
  .checkout-btn:hover:after, .checkout-btn:focus:after, .checkout-btn:active:after {
    right: -10px;
  }
  .promoCode .btn {
    font-size: .85em;

  }
  
  /* TOTAL AND CHECKOUT  */
  .subtotal {
    float: right;
    width: 35%;
  }
  .subtotal .totalRow {
    padding: .5em;
    text-align: right;
  }
  .subtotal .totalRow.final {
    font-size: 1.25em;
    font-weight: bold;
  }
  .subtotal .totalRow span {
    display: inline-block;
    padding: 0 0 0 1em;
    text-align: right;
  }
  .subtotal .totalRow .label {
    font-family: "Montserrat", sans-serif;
    font-size: .85em;
    text-transform: uppercase;
    color: #777;
  }
  .subtotal .totalRow .value {
    letter-spacing: -.025em;
    width: 35%;
  }
  
  @media only screen and (max-width: 39.375em) {
    .wrap {
      width: 98%;
      padding: 2% 0;
      padding-top: 70px;
    }
  
    .checkout-title {
      font-size: 1.5em;
      padding: 10% 5%;
    }
  
    .heading {
      padding: 1em;
      font-size: 90%;
    }
  
    .cart .items .cartSection {
      width: 90%;
      display: block;
      float: left;
    }
    .cart .items .cartSection.qtyWrap {
      width: 10%;
      text-align: center;
      padding: .5em 0;
      float: right;
    }
    .cart .items .cartSection.qtyWrap:before {
      content: "QTY";
      display: block;
      font-family: "Montserrat", sans-serif;
      padding: .25em;
      font-size: .75em;
    }
    .cart .items .cartSection.prodTotal, .cart .items .cartSection.removeWrap {
      display: none;
    }
    .cart .items .cartSection .itemImg {
      width: 25%;
    }
  
    .promoCode, .subtotal {
      width: 100%;
    }
  
    a.btn.continue {
      width: 100%;
      text-align: center;
    }
  }
  